import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import { getBedType,getUser,getHospital } from '../Apis';
// import HospitalAdmission from '../QrCode/QrCode';

//import bedtypesData from './bedtypesData'

class BedType extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      showHospitalInfo:false
    };
  }

  componentDidMount() {
    this.getBedType(this.props.match.params.id)
  
  }
  getBedType=async(id)=>{
    const responseData= await getBedType(id);
    console.log(responseData)
      if(responseData.status == 200){
        this.setState({
           
          items: responseData.data
        });
        this.getUser( responseData.data.created_by,'creator')
        this.getUser( responseData.data.modified_by,'Modifier')
        this.getHospital( responseData.data.hospital_id)
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  getUser=async(id,user)=>{
    
    const responseData= await getUser(id);
    console.log(responseData,id,'response in creator')
      if(user=='creator')
      {
        if(responseData.status==200)
          {
            this.setState({
              isLoaded: true,
              creatorName: responseData.data.user.first_name+' '+ responseData.data.user.last_name
            });
           
         
        }
        else{
          this.setState({
            creatorName:'Creator has been removed'
        });
        }
       
      }
      else if(user=='Modifier'){
        
        if(responseData.status==200)
        {
          this.setState({
            modifierName: responseData.data.user.first_name+' '+ responseData.data.user.last_name
          });
        }
        else{
          this.setState({
            modifierName:'Not yet modified by any user'
          });
         
        }
      }
      
      
  }
  getHospital=async(id)=>{
    const responseData= await getHospital(id);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          hospitalName: responseData.data.name
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  showHospitalInfo=()=>{
    this.setState({showHospitalInfo:true})
  }
  render() {

    const bedtype = this.state.items; //bedtypesData.find( bedtype => bedtype.id.toString() === this.props.match.params.id)

    let roleName = '-';

    if(bedtype.roles !== undefined && bedtype.roles.length !== 0){
      roleName = bedtype.roles[0].name;
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={3}>
          </Col>
          <Col lg={6}>
            <Card>
              <CardHeader>
                <strong><i className="icon-info pr-1"></i>BedType Id: {this.props.match.params.id}</strong>
                {/* <button onClick={this.showHospitalInfo} className='btn btn-success  bedtypebtn'>Enter Admission Info</button> */}
              </CardHeader>
              <CardBody>
              
                  <Table responsive striped hover>
                  <tbody>                    
                    <tr>
                      <td>Name:</td>
                      <td><strong>{bedtype.name}</strong></td>
                    </tr>
                    <tr>
                      <td>Description:</td>
                      <td><strong>{bedtype.description}</strong></td>
                    </tr>
                    <tr>
                      <td>Hospital Name:</td>
                      <td><strong>{this.state.hospitalName}</strong></td>
                    </tr>
                   
                    <tr>
                      <td>Created By:</td>
                      <td><strong>{this.state.creatorName}</strong></td>
                    </tr>
                    <tr>
                      <td>Modified By:</td>
                      <td><strong>{this.state.modifierName}</strong></td>
                    </tr>
               
                    <tr>
                      <td>Status:</td>
                      <td><strong>{bedtype.status==1?'Enable':'Disable'}</strong></td>
                    </tr>
                    <tr>
                      <td>Status Reason:</td>
                      <td><strong>{bedtype.status_reason}</strong></td>
                    </tr>
                  
                
                  </tbody>
                </Table>
              
                 
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default BedType;
